
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.site-layout-content {
  min-height: 500px;
  padding: 24px;
  background: rgb(0, 0, 0);
}
.menu{
  z-index:1;
}

.nameheader{
  font-size:60px;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  font-weight: bolder;
  color:lemonchiffon;;
  position: absolute;
  -webkit-text-stroke-width: 3px;
  -webkit-text-stroke-color:black;
  left: 360px;
  bottom: 330px;
  z-index:3;
}
.resumeheader
{
  font-size:40px;
  font-family:Georgia, 'Times New Roman', Times, serif;
  font-weight: bolder;
  color:black;
  position: absolute;
  left: 440px;
  bottom: 285px;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color:lemonchiffon;
  z-index:2;
}
.bigImage { 
  width: 100%;
  height: 569px;
  opacity: 1;
  z-index: 2;

}